import React from 'react';
import { ChevronLeft, ChevronRight } from 'lucide-react';

const CarouselTwo = () => {
  return (
    <div className='relative w-full max-w-4xl overflow-hidden flex items-center bg-white shadow-lg rounded-lg p-6'>
      <button className='absolute left-2 z-10 p-2 bg-gray-200 rounded-full'>
        <ChevronLeft />
      </button>
      <div className='flex gap-6 overflow-x-scroll scrollbar-hide'>
        {[...Array(3)].map((_, index) => (
          <div key={index} className='min-w-[320px] h-[180px] p-6 bg-gray-200 shadow-md rounded-lg flex flex-col justify-center items-center text-lg font-medium'>
            <h2 className='font-bold mb-2'>Item {index + 1}</h2>
            <p>Lorem ipsum dolor sit amet.</p>
          </div>
        ))}
      </div>
      <button className='absolute right-2 z-10 p-2 bg-gray-200 rounded-full'>
        <ChevronRight />
      </button>
    </div>
  );
};

export default CarouselTwo;
